import {
  Directive
} from './binding'
import directives from './directives'
import {
  DomParser,
  DirectiveParser,
  TextParser,
  expression
} from './parser'
import configuration from './configuration'
import {
  configuration as observiConfiguration
} from 'observi'
import {
  dynamicClass,
  hasOwnProp,
  ConfigurationChain
} from 'ilos'

const config = new ConfigurationChain(configuration, observiConfiguration)

let templateId = 0
const templateCache = {}
let inited = false
const Template = dynamicClass({
  statics: {
    get(id) {
      return templateCache[id]
    }
  },
  constructor(templ, cfg = {}) {
    if (!inited) {
      configuration.nextStatus()
      inited = true
    }
    this.id = cfg.id || (templateId++)
    if (hasOwnProp(templateCache, this.id)) {
      throw new Error('Existing Template[' + this.id + ']')
    }
    this.parser = new DomParser(templ)
    templateCache[this.id] = this
  },
  complie(scope) {
    return this.parser.complie(scope)
  }
})
export {
  Template,
  Directive,
  directives,
  DirectiveParser,
  TextParser,
  expression,
  config as configuration
}
